import Home from "~/components/pages/Home.vue";

const sampleData = [
  {
    id: 1,
    name: "",
    position: "",
    picture: "/images/banner.jpg",
    difficulty: "Easy",
    addition_amount: "High",
    leaf_length: 2,
    water_quality: "  ",
  },
  {
    id: 2,
    name: "",
    position: "",
    picture: "/images/banner.jpg",
    difficulty: "Hard",
    addition_amount: "High",
    leaf_length: 1,
    water_quality: "  ",
  },
  {
    id: 3,
    name: "",
    position: "",
    picture: "/images/banner.jpg",
    difficulty: "Medium",
    addition_amount: "High",
    leaf_length: 1,
    water_quality: "  ",
  },
];

export default {
  components: {
    Home,
  },
  data() {
    return {
      menus: [],
      otherMenus: [],
    };
  },
  async asyncData({ $axios, params }) {
    try {
      const menus = await $axios.$get(`/menu/`);
      const otherMenus = await $axios.$get(`/other-menu/`);
      return { menus, otherMenus };
    } catch (e) {
      const data = sampleData;
      return {
        menus: data,
        otherMenus: data,
      };
    }
  },
};
